.card {
  width: 45%;
  min-width: 400px;
  margin: 3px auto;
  padding: 15px 0;
  border: 1px solid lightgray;
  border-radius: 6px;
  background-color: white;
  box-shadow: 2px 6px 8px 3px rgba(0, 0, 0, 0.3);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.leftSide{
  width: 65%;
  text-align: center;
}
.wordNumber{
  text-align: left;
  font-size: 0.5rem;
  margin: 0;
  padding: 0;
  padding-left: 9px;
}
.vocabItem {
  font-size: 2rem;
  margin: 30px 0;
}
.vocabAnswer {
  font-size: 1.5rem;
  font-style: italic;
  margin: 30px 0;
  opacity: 1;
	-webkit-animation-name: fadeInAnswer;
	        animation-name: fadeInAnswer;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}
@-webkit-keyframes fadeInAnswer {
  0% {	opacity: 0; }
  80% { opacity: 0; }
	100% { opacity: 1; }
}
@keyframes fadeInAnswer {
  0% {	opacity: 0; }
  80% { opacity: 0; }
	100% { opacity: 1; }
}

.rightSide {
  width: 35%;
